.app {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 130vh;
  background: linear-gradient(
      rgba(15, 5, 5, 0.587),
      rgba(31, 6, 222, 0.619)
    );
  background-size: cover;
  background-position: center;
}

form{
    background-color: white;
    padding: 0px 60px;
    border-radius: 10px;
}

h1{
    color: rgb(77, 1, 77);
    text-align: center;
}

button{
    width: 100%;
    height: 50px;
    padding: 10px;
    border: none;
    background-color: rebeccapurple;
    color: white;
    border-radius: 5px;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 30px;
}